import { Injectable } from "@angular/core";
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from "@angular/router";
import { Observable } from "rxjs";
import { ToastrService } from "ngx-toastr";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "./state/loggedinuser.state";
import { LoggedInUser } from "./state/model/loggedinuser.model";

@Injectable({
	providedIn: "root",
})
export class PermissionGuard implements CanActivate {
	userType$: string;
	userTypeId$: number;
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;

	constructor(private toastr: ToastrService) {
		this.loggedInUser$.subscribe((resp) => {
			this.userType$ = resp.LoggedInUser.userType;
			this.userTypeId$ = resp.LoggedInUser.userTypeId;
		});
	}

	canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
		if (route.data.permission.indexOf(this.userType$) > -1) {
			return true;
		} else {
			this.toastr.warning("You have no access to view this page.", "Access Denied");
			return false;
		}
	}
}
