import { Component, OnInit } from "@angular/core";
import { Location } from "@angular/common";
import { ActivatedRoute, Router } from "@angular/router";
import { SolutionsService } from "../../services/solutions.service";
import { environment } from "../../../environments/environment";
import { FormGroup, FormBuilder } from "@angular/forms";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../state/loggedinuser.state";
import { Observable } from "rxjs";
import { ToastrService } from "ngx-toastr";

@Component({
	selector: "app-solution",
	templateUrl: "./solution.component.html",
	styleUrls: ["./solution.component.css"],
})
export class SolutionIndividualComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;

	id: any;
	solution: any;
	loading: boolean;
	server_url: string = environment.server_url;
	imagesLoading: boolean = true;
	currentSeekerId: any;
	isSubmitted: any = {
		Step1: false,
		Step2: false,
		Step3: false,
		Step4: false,
		Step5: false,
		Step6: false,
	};
	eoiStep1: boolean;
	eoiStep2: boolean;
	eoiStep3: boolean;
	eoiStep4: boolean;
	eoiStep5: boolean;
	eoiStep6: boolean;
	backToStep: string;
	eoiForm: any = {
		Step1: FormGroup,
		Step2: FormGroup,
		Step3: FormGroup,
		Step4: FormGroup,
		Step5: FormGroup,
		Step6: FormGroup,
	};

	constructor(
		private route: ActivatedRoute,
		private solutions: SolutionsService,
		private location: Location,
		private router: Router,
		private fb: FormBuilder,
		private toastr: ToastrService
	) {
		this.route.params.subscribe((params) => (this.id = params.id));
		this.loggedInUser$.subscribe((resp) => {
			this.currentSeekerId = resp.LoggedInUser.userTypeId;
		});
	}

	ngOnInit() {
		this.eoiStep1 = false;
		this.eoiStep2 = true;
		this.eoiStep3 = true;
		this.eoiStep4 = true;
		this.eoiStep5 = true;
		this.eoiStep6 = true;
		this.backToStep = "step1";
		this.eoiForm = {
			Step1: this.fb.group({
				state: [""],
				city: [""],
				pincode: [""],
			}),
		};
		this.getSolution();
	}

	get formControlStep1() {
		return this.eoiForm.Step1.controls;
	}
	get formControlStep2() {
		return this.eoiForm.Step2.controls;
	}
	get formControlStep3() {
		return this.eoiForm.Step3.controls;
	}
	get formControlStep4() {
		return this.eoiForm.Step4.controls;
	}
	get formControlStep5() {
		return this.eoiForm.Step5.controls;
	}
	get formControlStep6() {
		return this.eoiForm.Step6.controls;
	}

	async getSolution() {
		try {
			this.loading = true;
			this.solution = await this.solutions.getSolutionbyId(this.id);
			this.loading = false;
		} catch (err) {
			console.log(err);
		}
	}

	goBack() {
		if (window.history.length > 1) {
			this.location.back();
		} else {
			this.router.navigate(["/discover/solutions"]);
		}
	}

	generateFake(count: number): Array<number> {
		const indexes = [];
		for (let i = 0; i < count; i++) {
			indexes.push(i);
		}
		return indexes;
	}

	stepFormSubmit(formIndex: string) {
		switch (formIndex) {
			case "Step1": {
				this.isSubmitted.Step1 = true;
				this.ShowNextStep(formIndex);
				break;
			}
			case "Step2": {
				this.isSubmitted.Step2 = true;
				this.ShowNextStep(formIndex);
				break;
			}
			case "Step3": {
				this.isSubmitted.Step3 = true;
				this.ShowNextStep(formIndex);
				break;
			}
			case "Step4": {
				this.isSubmitted.Step4 = true;
				this.ShowNextStep(formIndex);
				break;
			}
			case "Step5": {
				this.isSubmitted.Step5 = true;
				this.ShowNextStep(formIndex);
				break;
			}
			case "Step6": {
				this.isSubmitted.Step6 = true;
				this.ShowNextStep(formIndex);
			}
		}
	}

	ShowNextStep(formIndex: any) {
		if (formIndex == "Step1" && this.eoiForm.Step1.valid) {
			this.gotoStep("step2");
		} else if (formIndex == "Step2" && this.eoiForm.Step2.valid) {
			this.gotoStep("step3");
		} else if (formIndex == "Step3" && this.eoiForm.Step3.valid) {
			this.gotoStep("step4");
		} else if (formIndex == "Step4" && this.eoiForm.Step4.valid) {
			this.gotoStep("step5");
		} else if (formIndex == "Step5" && this.eoiForm.Step5.valid) {
			this.gotoStep("step6");
		} else if (formIndex == "Step6" && this.eoiForm.Step6.valid) {
			// this.createAnchor();
		} else {
			this.toastr.error("", "Please provide the required values to proceed further");
		}
	}

	gotoStep(step: any) {
		switch (step) {
			case "step1": {
				this.eoiStep1 = false;
				this.eoiStep2 = true;
				this.eoiStep3 = true;
				this.eoiStep4 = true;
				this.eoiStep5 = true;
				this.eoiStep6 = true;
				break;
			}
			case "step2": {
				this.eoiStep1 = true;
				this.eoiStep2 = false;
				this.eoiStep3 = true;
				this.eoiStep4 = true;
				this.eoiStep5 = true;
				this.eoiStep6 = true;
				this.backToStep = "step1";
				break;
			}
			case "step3": {
				this.eoiStep1 = true;
				this.eoiStep2 = true;
				this.eoiStep3 = false;
				this.eoiStep4 = true;
				this.eoiStep5 = true;
				this.eoiStep6 = true;
				this.backToStep = "step2";
				break;
			}
			case "step4": {
				this.eoiStep1 = true;
				this.eoiStep2 = true;
				this.eoiStep3 = true;
				this.eoiStep4 = false;
				this.eoiStep5 = true;
				this.eoiStep6 = true;
				this.backToStep = "step3";
				break;
			}
			case "step5": {
				this.eoiStep1 = true;
				this.eoiStep2 = true;
				this.eoiStep3 = true;
				this.eoiStep4 = true;
				this.eoiStep5 = false;
				this.eoiStep6 = true;
				this.backToStep = "step4";
				break;
			}
			case "step6": {
				this.eoiStep1 = true;
				this.eoiStep2 = true;
				this.eoiStep3 = true;
				this.eoiStep4 = true;
				this.eoiStep5 = true;
				this.eoiStep6 = false;
				this.backToStep = "step5";
				break;
			}
		}
	}
}
